.wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 40px;
	-ms-flex-wrap: wrap;
		flex-wrap: wrap;
	-webkit-transform: translateY(-50%);
			transform: translateY(-50%);
  }
  
  input[type="checkbox"].switch_1{
	font-size: 15px;
	-webkit-appearance: none;
	   -moz-appearance: none;
			appearance: none;
	width: 2.5em;
	height: 1.5em;
	background: #ddd;
	border-radius: 3em;
	position: relative;
	cursor: pointer;
	outline: none;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
	}
	
	input[type="checkbox"].switch_1:checked{
		background: rgb(246, 6, 66)
	}
	
	input[type="checkbox"].switch_1:after{
	position: absolute;
	content: "";
	width: 1.5em;
	height: 1.5em;
	border-radius: 50%;
	background: #fff;
	-webkit-box-shadow: 0 0 .25em rgba(0,0,0,.3);
			box-shadow: 0 0 .25em rgba(0,0,0,.3);
	-webkit-transform: scale(.7);
			transform: scale(.7);
	left: 0;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
	}
	
	input[type="checkbox"].switch_1:checked:after{
	left: calc(100% - 1.5em);
	}